import React from 'react'
import courses from '../data.json'
import { CourseListRead, CourseListEdit } from '../components/course-list'
import withEdit from '../hooks/withEdit.jsx'

export default withEdit(props =>
  !props.edit ? (
    <CourseListRead courses={courses} {...props} />
  ) : (
    <CourseListEdit courses={courses} {...props} />
  )
)
