import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItemText from '@material-ui/core/ListItemText'
import { makeStyles } from '@material-ui/core/styles'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'
import React from 'react'
import { Avatar, Link, Typography } from '../editable'

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper
  },
  listItem: {
    borderBottomColor: theme.palette.grey[400],
    borderBottomWidth: 1,
    borderBottomStyle: 'solid'
  }
}))

export default function CourseList({ courses, renderCourseAction }) {
  const classes = useStyles()

  return (
    <List className={classes.root}>
      {courses.map((course, index) => (
        <Link to={`/course/${course.id}`} key={course.id}>
          <ListItem className={classes.listItem} alignItems='flex-start'>
            <ListItemAvatar>
              <Avatar alt='Remy Sharp' src={course.image} name={`courses[${index}].image`} />
            </ListItemAvatar>
            <ListItemText
              primary={<Typography name={`courses[${index}].name`}>{course.name}</Typography>}
            />
            {renderCourseAction(course, index)}
            <ArrowForwardIosIcon />
          </ListItem>
        </Link>
      ))}
    </List>
  )
}

CourseList.defaultProps = {
  renderCourseAction: () => {}
}
