import * as Yup from 'yup'

const name = Yup.string()
  .max(15, 'Must be 15 characters or less')
  .required('Required')

export default {
  course: {
    name: name,
    image: Yup.string()
      // .max(15, 'Must be 15 characters or less')
      .required('Required')
  },
  topic: {
    name: name
  },
  activity: {
    name: name
  }
}
