import { FieldArray, Form, Formik } from 'formik'
import React from 'react'
import * as Yup from 'yup'
import validationSchema from '../../utils/validation'
import TopicRead from './topic-read'
import Button from '@material-ui/core/Button'
import { ListActionButtons } from '..'

const schema = Yup.object().shape({
  topic: Yup.object().shape({
    levels: Yup.array().of(
      Yup.object().shape({
        name: validationSchema.topic.name
      })
    )
  })
})

export default function TopicEdit({ topic, edit }) {
  const FormContent = props => (
    <>
      <TopicRead
        topic={props.values.topic}
        renderTopicAction={(topic, index) => (
          <ListActionButtons name='topic.levels' index={index} {...props} />
        )}
      />
      <Button
        variant='contained'
        color='secondary'
        onClick={() =>
          props.arrayHelpers.push({
            id: props.values.topic.levels.length + 1,
            name: '',
            image: '',
            order: props.values.topic.levels.length
          })
        }
      >
        Add a level
      </Button>
    </>
  )

  return (
    <Formik
      initialValues={{ topic: topic }}
      onSubmit={values => alert(JSON.stringify(values, null, 2))}
      validationSchema={schema}
    >
      {({ submitForm, ...props }) => (
        <Form>
          <FieldArray name='topic.levels'>
            {arrayHelpers => <FormContent arrayHelpers={arrayHelpers} {...props} />}
          </FieldArray>
          <Button
            variant='contained'
            color='primary'
            onClick={() => {
              debugger
              submitForm()
            }}
          >
            Submit
          </Button>
        </Form>
      )}
    </Formik>
  )
}
