import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItemText from '@material-ui/core/ListItemText'
import { makeStyles } from '@material-ui/core/styles'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'
import React from 'react'
import { Avatar, Link, Typography } from '../editable'

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  },
  listItem: {
    borderBottomColor: theme.palette.grey[400],
    borderBottomWidth: 1,
    borderBottomStyle: 'solid'
  }
}))

export default function CourseRead({ course, renderTopicAction }) {
  const classes = useStyles()

  return (
    <>
      Course: {course.name}
      <List className={classes.root}>
        {course.topics.map((topic, index) => (
          <Link to={`/course/${course.id}/topic/${topic.id}`} key={topic.id}>
            <ListItem className={classes.listItem} alignItems='flex-start'>
              <ListItemAvatar>
                <Avatar alt='Remy Sharp' src={topic.image} name={`course.topics[${index}].image`} />
              </ListItemAvatar>
              <ListItemText
                primary={
                  <Typography name={`course.topics[${index}].name`}>{topic.name}</Typography>
                }
              />
              {renderTopicAction(topic, index)}
              <ArrowForwardIosIcon />
            </ListItem>
          </Link>
        ))}
      </List>
    </>
  )
}

CourseRead.defaultProps = {
  renderTopicAction: () => {}
}
