import React from 'react'
import courses from '../data.json'
import { ActivitiesRead, ActivitiesEdit } from '../components'
import withEdit from '../hooks/withEdit.jsx'
import { useParams } from 'react-router-dom'

export default withEdit(props => {
  const { courseId, topicId, levelId } = useParams()
  const course = courses.find(course => course.id === parseInt(courseId))
  const topic = course.topics.find(topic => topic.id === parseInt(topicId))
  const level = topic.levels.find(level => level.id === parseInt(levelId))
  const activityProps = { course, level }
  return !props.edit ? <ActivitiesRead {...activityProps} /> : <ActivitiesEdit {...activityProps} />
})
