import React from 'react'
import IconButton from '@material-ui/core/IconButton'
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import DeleteIcon from '@material-ui/icons/Delete'
import { getIn } from 'formik'

export default ({ values, name, index, setFieldValue, arrayHelpers }) => {
  const swipUp = () => {
    if (index > 0) {
      setFieldValue(`${name}[${index}].order`, getIn(values, name)[index - 1].order)
      setFieldValue(`${name}.topics[${index - 1}].order`, getIn(values, name)[index].order)
      arrayHelpers.swap(index - 1, index)
    }
  }

  const swipDown = () => {
    if (index < values.length - 1) {
      setFieldValue(`${name}[${index}].order`, getIn(values, name)[index + 1].order)
      setFieldValue(`${name}[${index + 1}].order`, getIn(values, name)[index].order)
      arrayHelpers.swap(index + 1, index)
    }
  }

  const deleteCourse = () => {
    arrayHelpers.remove(index)
  }

  return (
    <>
      <IconButton size='small' onClick={swipDown}>
        <ArrowDownwardIcon fontSize='inherit' />
      </IconButton>
      <IconButton size='small' onClick={swipUp}>
        <ArrowUpwardIcon fontSize='inherit' />
      </IconButton>
      <IconButton size='small' onClick={deleteCourse}>
        <DeleteIcon fontSize='inherit' />
      </IconButton>
    </>
  )
}
