import Typography from '@material-ui/core/Typography'
import { Field } from 'formik'
import { TextField } from 'formik-material-ui'
import React from 'react'
import withEdit from '../../hooks/withEdit'

export default withEdit(({ edit, ...props }) => {
  return edit ? <Field component={TextField} name={props.name} /> : <Typography {...props} />
})
