import React from 'react'
import LinearProgress from '@material-ui/core/LinearProgress'
import Typography from '@material-ui/core/Typography'
import { makeStyles, useTheme } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import HighlightOffIcon from '@material-ui/icons/HighlightOff'
import Button from '@material-ui/core/Button'
import Link from '@material-ui/core/Link'
import Grid from '@material-ui/core/Grid'
import grey from '@material-ui/core/colors/grey'
import green from '@material-ui/core/colors/green'
import red from '@material-ui/core/colors/red'
import clsx from 'clsx'
import MobileStepper from '@material-ui/core/MobileStepper'
import Paper from '@material-ui/core/Paper'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'

import activityTypes from './activity'

function adjustActivities(level, course) {
  return level.activities.map(activity => {
    if (activity.type === 'template') {
      activity = {
        ...activity,
        ...course.defaultQuestions.find(dq => dq.id === activity.templateId),
        id: activity.id
      }
    }
    return activity
  })
}

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: 'calc( 100vh - 200px)',
    // maxWidth: 400,
    flexGrow: 1
  },
  main: {
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(2)
  },
  footer: {
    padding: theme.spacing(2),
    marginTop: 'auto',
    backgroundColor: 'white'
  },
  // header: {
  //   display: 'flex',
  //   alignItems: 'center',
  //   height: 50,
  //   paddingLeft: theme.spacing(4),
  //   backgroundColor: theme.palette.background.default
  // },
  img: {
    overflow: 'hidden',
    display: 'block'
  }
}))

export default ({ course, level }) => {
  const [completed, setCompleted] = React.useState(0)
  const [activities, setActivities] = React.useState(adjustActivities(level, course))
  const classes = useStyles()

  const theme = useTheme()
  const [activeStep, setActiveStep] = React.useState(0)
  const maxSteps = activities.length

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1)
    setCompleted(((activeStep + 1) * 100) / activities.length)
  }

  const completeActivity = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1)
  }

  const Activity = activityTypes[activities[activeStep].type]

  return (
    <div className={classes.root}>
      <div>
        <Paper square elevation={0} className={classes.header}>
          <Activity activity={activities[activeStep]} completeActivity={completeActivity}>
            {activities[activeStep].id}
          </Activity>
        </Paper>
      </div>
      <footer className={classes.footer}>
        <MobileStepper
          steps={maxSteps}
          position='static'
          variant='text'
          activeStep={activeStep}
          nextButton={
            <Button size='small' onClick={handleNext} disabled={activeStep === maxSteps - 1}>
              Next
              {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </Button>
          }
        />
        <LinearProgress variant='determinate' value={completed} color='secondary' />
      </footer>
    </div>
  )
}
