import { FieldArray, Form, Formik } from 'formik'
import React from 'react'
import * as Yup from 'yup'
import validationSchema from '../../utils/validation'
import CourseRead from './course-read'
import Button from '@material-ui/core/Button'
import { ListActionButtons } from '../'

const schema = Yup.object().shape({
  course: Yup.object().shape({
    topics: Yup.array().of(
      Yup.object().shape({
        name: validationSchema.course.name,
        image: validationSchema.course.image
      })
    )
  })
})

export default function CourseEdit({ course, edit }) {
  const FormContent = props => (
    <>
      <CourseRead
        course={props.values.course}
        renderTopicAction={(course, index) => (
          <ListActionButtons name='course.topics' index={index} {...props} />
        )}
      />
      <Button
        variant='contained'
        color='secondary'
        onClick={() =>
          props.arrayHelpers.push({
            id: Math.max(...props.values.course.topics) + 1,
            name: '',
            image: '',
            order: props.values.course.topics.length
          })
        }
      >
        Add a Topic
      </Button>
    </>
  )

  return (
    <Formik
      initialValues={{ course: course }}
      onSubmit={values => alert(JSON.stringify(values, null, 2))}
      validationSchema={schema}
    >
      {({ submitForm, ...props }) => (
        <Form>
          <FieldArray name='course.topics'>
            {arrayHelpers => <FormContent arrayHelpers={arrayHelpers} {...props} />}
          </FieldArray>
          <Button
            variant='contained'
            color='primary'
            onClick={() => {
              debugger
              submitForm()
            }}
          >
            Submit
          </Button>
        </Form>
      )}
    </Formik>
  )
}
