import React from 'react'
import courses from '../data.json'
import { CourseRead, CourseEdit } from '../components/course'
import withEdit from '../hooks/withEdit.jsx'
import { useParams } from 'react-router-dom'

export default withEdit(props => {
  const { id } = useParams()
  const course = courses.find(course => course.id === parseInt(id))
  return !props.edit ? (
    <CourseRead course={course} {...props} />
  ) : (
    <CourseEdit course={course} {...props} />
  )
})
