import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItemText from '@material-ui/core/ListItemText'
import { makeStyles } from '@material-ui/core/styles'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'
import React from 'react'
import { Link, Typography } from '../editable'
import Rating from '@material-ui/lab/Rating'

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  },
  listItem: {
    borderBottomColor: theme.palette.grey[400],
    borderBottomWidth: 1,
    borderBottomStyle: 'solid'
  }
}))

export default function TopicRead({ topic, renderTopicAction }) {
  const classes = useStyles()

  return (
    <>
      Topic: {topic.name}
      <List className={classes.root}>
        {topic.levels.map((activity, index) => (
          <Link to={`/course/2/topic/4/1`} key={activity.id}>
            <ListItem className={classes.listItem} alignItems='flex-start'>
              <Rating value={3} readOnly />
              {renderTopicAction(topic, index)}
              <ArrowForwardIosIcon />
            </ListItem>
          </Link>
        ))}
      </List>
    </>
  )
}

TopicRead.defaultProps = {
  renderTopicAction: () => {}
}
