import React from 'react'
import LinearProgress from '@material-ui/core/LinearProgress'
import Typography from '@material-ui/core/Typography'
import { makeStyles, useTheme } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import HighlightOffIcon from '@material-ui/icons/HighlightOff'
import Button from '@material-ui/core/Button'
import Link from '@material-ui/core/Link'
import Grid from '@material-ui/core/Grid'
import grey from '@material-ui/core/colors/grey'
import green from '@material-ui/core/colors/green'
import red from '@material-ui/core/colors/red'
import clsx from 'clsx'
import MobileStepper from '@material-ui/core/MobileStepper'
import Paper from '@material-ui/core/Paper'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'

const useStyles = makeStyles(theme => ({
  root: {
    // padding: theme.spacing(8)
    // display: 'flex',
    // flexDirection: 'column'
    // minHeight: 'calc( 100vh - 200px)'
  },
  main: {
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(2)
  },
  footer: {
    padding: theme.spacing(2),
    marginTop: 'auto',
    backgroundColor: 'white'
  },
  choices: {
    backgroundColor: grey[400],
    color: `${theme.palette.getContrastText(theme.palette.grey[300])} !important`
  },
  correct: {
    backgroundColor: `${green[600]} !important`
  },
  wrong: {
    backgroundColor: `${red[600]} !important`
  }
}))

export default ({ activity: originalActivity }) => {
  const [choosed, setChoosed] = React.useState('')
  const [activity, setActivity] = React.useState(originalActivity)

  const classes = useStyles()
  const showIcon = (choice, index) => !!choosed && (index === 0 || choosed === choice)

  return (
    <div className={classes.root}>
      <audio controls>
        <source src={activity.voice} type='audio/mp3' />
      </audio>
      <Typography>{activity.label}</Typography>
      <Grid container spacing={3}>
        {activity.extra.choices.map((choice, index) => (
          <Grid item xs={4}>
            <Button
              disabled={!!choosed}
              variant='contained'
              startIcon={
                showIcon(choice, index) ? (
                  index === 0 ? (
                    <CheckCircleIcon />
                  ) : (
                    <HighlightOffIcon />
                  )
                ) : null
              }
              className={clsx(classes.choices, {
                [classes.correct]: index === 0 && !!choosed,
                [classes.wrong]: index !== 0 && choosed === choice
              })}
              onClick={() => setChoosed(choice)}
            >
              {choice}
            </Button>
          </Grid>
        ))}
      </Grid>
    </div>
  )
}
