import { FieldArray, Form, Formik } from 'formik'
import React from 'react'
import * as Yup from 'yup'
import validationSchema from '../../utils/validation'
import CourseListRead from './course-list-read'
import Button from '@material-ui/core/Button'
import { ListActionButtons } from '../'

const schema = Yup.object().shape({
  courses: Yup.array().of(
    Yup.object().shape({
      name: validationSchema.course.name,
      image: validationSchema.course.image
    })
  )
})

export default function CourseList({ courses, edit }) {
  const FormContent = props => (
    <>
      <CourseListRead
        courses={props.values.courses}
        renderCourseAction={(course, index) => (
          <ListActionButtons name='courses' index={index} {...props} />
        )}
      />
      <Button
        variant='contained'
        color='secondary'
        onClick={() =>
          props.arrayHelpers.push({
            id: courses.length + 1,
            name: '',
            image: '',
            order: courses.length
          })
        }
      >
        Add a course
      </Button>
    </>
  )

  return (
    <Formik
      initialValues={{ courses: courses }}
      onSubmit={values => alert(JSON.stringify(values, null, 2))}
      validationSchema={schema}
    >
      {({ submitForm, ...props }) => (
        <Form>
          <FieldArray name='courses'>
            {arrayHelpers => <FormContent arrayHelpers={arrayHelpers} {...props} />}
          </FieldArray>
          <Button
            variant='contained'
            color='primary'
            onClick={() => {
              submitForm()
            }}
          >
            Submit
          </Button>
        </Form>
      )}
    </Formik>
  )
}
