import React, { Fragment, useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import style from './style'
import Grid from '@material-ui/core/Grid'

const useStyles = makeStyles(style)

const DragArea = ({ taken, s, index, dropElement }) => {
  const [boxRight, setBoxRight] = useState(0)
  const [boxTop, setBoxTop] = useState(0)
  const [startX, setStartX] = useState(0)
  const [startY, setStartY] = useState(0)
  const [right, setRight] = useState(30)
  const [top, setTop] = useState(30 * index + 40)
  // const [top, setTop] = useState(0)

  const classes = useStyles()

  const onDragStart = (ev, id, index) => {
    id = ' ' + id + ' '
    ev.dataTransfer.setData('id', JSON.stringify({ id, index }))
  }

  const touchMove = e => {
    var touchobj = e.changedTouches[0] // reference first touch point for this event
    var dist = parseInt(touchobj.clientX) - startX
    var distY = parseInt(touchobj.clientY) - startY
    setRight(boxRight - dist > 0 ? boxRight - dist : 0)
    setTop(boxTop + distY > 380 ? 380 : boxTop + distY < 0 ? 0 : boxTop + distY)
  }

  const touchStart = e => {
    const touchobj = e.changedTouches[0] // reference first touch point
    setBoxRight(parseInt(e.target.style.right)) // get right position of box
    setBoxTop(parseInt(e.target.style.top)) // get right position of box
    setStartX(parseInt(touchobj.clientX)) // get x coord of touch point
    setStartY(parseInt(touchobj.clientY)) // get x coord of touch point
  }

  const touchEnd = e => {
    if (!dropElement(e.target.getBoundingClientRect(), s, index)) {
      setTop(30 * index + 40)
      setRight(30)
    }
  }

  if (taken) {
    return (
      <span
        style={{ right, top }}
        onDragStart={e => onDragStart(e, s, index)}
        draggable
        className={classes.draggable}
        // onTouchStart={}
        onTouchMove={touchMove}
        onTouchStart={touchStart}
        onTouchEnd={touchEnd}
      >
        {s}
      </span>
    )
  } else {
    return null
  }
}

const DropArea = ({ s, index, choice, setChoice }) => {
  const onDragOver = ev => {
    ev.preventDefault()
  }

  const onDrop = (ev, dropIndex) => {
    let id = JSON.parse(ev.dataTransfer.getData('id'))
    choice[dropIndex] = id
    setChoice({ ...choice })
  }

  const classes = useStyles()
  debugger
  return (
    <span className={classes.wip} onDragOver={e => onDragOver(e)} onDrop={e => onDrop(e, index)}>
      <span
        key={s}
        // onDragStart={e => onDragStart(e, s, index)}
        draggable
        className={classes.droppable}
      >
        {(choice[index] && choice[index].id) || s.replace(/./g, '_')}
      </span>
    </span>
  )
}

export default ({ activity: originalActivity }) => {
  const [activity, setActivity] = React.useState(originalActivity)
  const [choice, setChoice] = React.useState({})

  const dropElement = (rect1, s, index) => {
    debugger
    const dropZones = document.getElementsByClassName(classes.droppable)
    for (let i = 0; i < dropZones.length; i++) {
      const rect2 = dropZones[i].getBoundingClientRect()
      const overlap = !(
        rect1.right < rect2.left ||
        rect1.left > rect2.right ||
        rect1.bottom < rect2.top ||
        rect1.top > rect2.bottom
      )
      if (overlap) {
        choice[i * 2 + 1] = { id: ' ' + s + ' ', index }
        setChoice({ ...choice })
      }
    }
    return false
  }

  const createDragArea = (s, index) => {
    const taken = !Object.values(choice).some(k => k.index == index)
    return <DragArea key={index} taken={taken} s={s} index={index} dropElement={dropElement} />
  }

  const createDropArea = (s, index) => (
    <DropArea key={index} s={s} index={index} choice={choice} setChoice={setChoice} />
  )

  const replaceNewLineWithBreak = s =>
    s.split('\n').map((item, key) => (
      <Fragment key={key}>
        {key > 0 && <br />}
        {item}
      </Fragment>
    ))

  const classes = useStyles()
  const strings = activity.extra
    .split('*')
    .map((s, index) =>
      index % 2 === 0 ? replaceNewLineWithBreak(s, index) : createDropArea(s, index)
    )
  const strings1 = activity.extra
    .split('*')
    .map((s, index) => (index % 2 === 1 ? createDragArea(s, index) : <br key={index} />))

  return (
    <Grid container spacing={3} className={classes.root}>
      <Grid item xs={6}>
        {strings1.map(str => str)}
      </Grid>
      <Grid item xs={6}>
        {strings.map(str => str)}
      </Grid>
      <Grid item xs={12}>
        <Button variant='contained' color='primary'>
          Check
        </Button>
      </Grid>
    </Grid>
  )
}
